// import common from "../../common/common.js";
//VM 协调者 调度器
import traillerStars from "../../components/traillerStars.vue"
export default {
	//Model 所有的数据
	data() {
		return {
			carouselList: [],
			hotsuperheroList: [],
			hotTrailerList:[],
			likeList:[],
			animationData:{
				
			},
			animationDataArr:[
				{},{},{},{},{},
			],
		
		}
	},
	onPullDownRefresh(){
		this.refresh();
	},
	onUnload() {
		//页面卸载的时候  清除动画数据
		this.animationData = {};
		animationDataArr: [
			{},{},{},{},{},
		]
	},
	onHide() {
		if(this.videoContext){
			this.videoContext.pause()
		}
	},
	onLoad() {
		
		
		//在页面创建的时候 创建一个临时动画对象
		this.animation = uni.createAnimation();
		//end
		
		// var serverUrl = common.serverUrl;
		//通过挂载到main.js 获取服务器地址作为全局变量
		var serverUrl = this.serverUrl;
		//请求轮播图数据
		uni.request({
		    url: serverUrl + '/index/carousel/list', //仅为示例，并非真实接口地址。
			method:"POST",
			header:{	'content-type':'application/x-www-form-urlencoded'	},
		    data: {
		        qq:'116521920'
		    },
		   
		    success: (res) => {
		        console.log(res.data);
				
				//获取真实数据之前 判断状态是否为200
				if(res.data.status == 200){
					var carouselList = res.data.data;
					this.carouselList = carouselList
				}
				
				
		       
		    }
		});
		//查询热门超英
		uni.request({
		    url: serverUrl + '/index/movie/hot?type=superhero', //仅为示例，并非真实接口地址。
			method:"POST",
			header:{	'content-type':'application/x-www-form-urlencoded'	},
		    data: {
		        qq:'116521920'
		    },
		   
		    success: (res) => {
		        // console.log(res.data);
				
				if(res.data.status == 200){
					var hotsuperheroList = res.data.data;
					this.hotsuperheroList = hotsuperheroList;
					
				}
				
			}
		});
		
		
			//查询热门预告
			
			uni.request({
				url: serverUrl + '/index/movie/hot?type=trailer',
				method: "POST",
				header:{
					'content-type':'application/x-www-form-urlencoded'
				},
				data:{
					qq:'116521920'
				},
				success: (res) => {
					// 获取真实数据之前，务必判断状态是否为200
					if (res.data.status == 200) {
						var hotTrailerList = res.data.data;
						this.hotTrailerList = hotTrailerList;
					}
			
				}
			});
		
		
		this.refresh();
	},
	methods: {
		//播放一个视频的时候需要暂停其他视频
		meIsPLay(e){
			var me = this;
			var trailerId = "";
			
			if(e){
				
				trailerId = e.currentTarget.dataset.playingindex;
				me.videoContext = uni.createVideoContext(trailerId);
			}
			var hotTrailerList = me.hotTrailerList;
			for(var i=0;i<hotTrailerList.length;i++){
				var tempId = hotTrailerList[i].id;
				if(tempId != trailerId){
					uni.createVideoContext(tempId).pause();
				}
			}
		},
		refresh(){
			uni.showLoading({
				mask: true
			});
			uni.showNavigationBarLoading()
			
			var serverUrl = this.serverUrl;
			uni.request({
			    url: serverUrl + '/index/guessULike', //仅为示例，并非真实接口地址。
				method:"POST",
				header:{	'content-type':'application/x-www-form-urlencoded'	},
			    data: {
			        qq:'116521920'
			    },
			   
			    success: (res) => {
			        console.log(res.data);
					
					//获取真实数据之前 判断状态是否为200
					if(res.data.status == 200){
						var likeList = res.data.data;
						this.likeList = likeList
					}
			    },
				complete: () => {
					uni.stopPullDownRefresh();
					uni.hideLoading();
					uni.hideNavigationBarLoading()
				}
			});
		
		},
		//实现点赞动画效果
		praiseMe(e){
			var gIndex = e.currentTarget.dataset.gindex;
			
			//构建动画数据 并且通过step表示这组动画完成
			this.animation.translateY(-60).opacity(1).step({
				duration: 400
			});
			//导出动画数据到view组件，实现组件的动画效果
			// this.animationData = this.animation.export();
			this.animationData = this.animation;
			this.animationDataArr[gIndex] = this.animationData.export();
			//还原动画
			setTimeout(function() {
				this.animation.translateY(0).opacity(0).step({
					duration: 0
				});
				this.animationData = this.animation;
				this.animationDataArr[gIndex] = this.animationData.export();
			}.bind(this), 500);
		}
	},
	components:{
		traillerStars
	}
}
